  // ===== MENU SCREENS =====
  
  private showMenu(): void {
    const screenContainer = document.getElementById('screen-container');
    if (!screenContainer) return;
    
    this.currentScreen = 'menu';
    const header = document.getElementById('game-header');
    if (header) header.style.display = 'none';
    
    screenContainer.innerHTML = `
      <div class="menu-screen">
        <h2 class="menu-title">🎯 Choose Game Mode</h2>
        <button class="menu-button" id="pvp-btn">👥 Player vs Player</button>
        <button class="menu-button" id="pvc-btn">🤖 Player vs Computer</button>
        <button class="menu-button" id="settings-btn">⚙️ Settings</button>
        <button class="menu-button" id="help-btn">❓ How to Play</button>
      </div>
    `;
    
    document.getElementById('pvp-btn')?.addEventListener('click', () => {
      this.startNewGame('pvp');
    });
    
    document.getElementById('pvc-btn')?.addEventListener('click', () => {
      this.showAIDifficultyMenu();
    });
    
    document.getElementById('settings-btn')?.addEventListener('click', () => {
      this.showSettings();
    });
    
    document.getElementById('help-btn')?.addEventListener('click', () => {
      this.showHelp();
    });
  }
  
  private showAIDifficultyMenu(): void {
    const screenContainer = document.getElementById('screen-container');
    if (!screenContainer) return;
    
    screenContainer.innerHTML = `
      <div class="menu-screen">
        <button class="back-button" id="back-btn" style="align-self: flex-start; margin-bottom: 20px;">← Back to Menu</button>
        <h2 class="menu-title">🤖 Select AI Difficulty</h2>
        <div class="difficulty-selector">
          <button class="difficulty-button active" data-diff="easy">Easy</button>
          <button class="difficulty-button" data-diff="medium">Medium</button>
          <button class="difficulty-button" data-diff="hard">Hard</button>
        </div>
        <p style="color: #ccc; text-align: center; margin: 20px 0;">
          <span id="diff-desc">Easy: AI makes random valid moves</span>
        </p>
        <button class="menu-button" id="start-ai-btn">Start Game</button>
      </div>
    `;
    
    let selectedDifficulty: 'easy' | 'medium' | 'hard' = 'easy';
    
    const diffButtons = document.querySelectorAll('.difficulty-button');
    const diffDesc = document.getElementById('diff-desc');
    
    const descriptions = {
      easy: 'Easy: AI makes random valid moves',
      medium: 'Medium: AI tries to maximize flips',
      hard: 'Hard: AI uses advanced strategy'
    };
    
    diffButtons.forEach(btn => {
      btn.addEventListener('click', () => {
        diffButtons.forEach(b => b.classList.remove('active'));
        btn.classList.add('active');
        selectedDifficulty = btn.getAttribute('data-diff') as any;
        if (diffDesc) {
          diffDesc.textContent = descriptions[selectedDifficulty];
        }
      });
    });
    
    document.getElementById('back-btn')?.addEventListener('click', () => {
      this.showMenu();
    });
    
    document.getElementById('start-ai-btn')?.addEventListener('click', () => {
      this.startNewGame('pvc', selectedDifficulty);
    });
  }
  
